import { AutosuggestSource } from "@rp/react/tanstack-api/content"
import { ContentCategory } from "@rp/react/tanstack-api/content"

function getSelectAllAutosuggestSource(category: ContentCategory | null): AutosuggestSource {
  const id = category?.id ?? ""
  const name = category?.name ?? ""

  return {
    category: "Sources",
    description: "dummy source for select all",
    entitled: true,
    entityType: "SRCE",
    group1: "",
    group2: "",
    group3: "",
    group4: "",
    group5: "",
    id,
    key: id,
    metadata1: "",
    metadata2: "",
    metadata3: "",
    metadata5: "",
    metadata7: [],
    name,
    queryType: "source",
    rpPackageId: [],
    typeDesc: "Source",
    sourceCategory: id ? [id] : [],
  }
}

export { getSelectAllAutosuggestSource }
